.portfolio-section {
  padding-top: 163px;
}
.portfolio-section .first-title-div {
  margin-bottom: 113px;
  display: grid;
  grid-template-columns: 40% 1fr 1fr;
}
.portfolio-section .first-title-div .col-one {
  grid-column: 1/2;
}
.portfolio-section .first-title-div .col-one .first-title {
  display: inline;
  font-size: 50px;
  line-height: 72px;
  font-weight: 400;
}
.portfolio-section .first-title-div .col-two {
  grid-column: 2/3;
  display: flex;
  align-items: flex-end;
}
.portfolio-section .first-title-div .col-two p {
  font-weight: 300;
  font-size: 14px;
  line-height: 159%;
}
.portfolio-section .first-title-div .col-three {
  grid-column: 3/4;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.portfolio-section .first-title-div .col-three .deco-icon {
  padding-left: 30px;
}
.portfolio-section .client-name {
  font-size: 50px;
  line-height: 72px;
  font-weight: 400;
  margin-bottom: 13px;
}
.portfolio-section .client-description1 {
  display: grid;
  grid-template-columns: 31% 69%;
  margin-bottom: 250px;
}
.portfolio-section .client-description1 .client-text {
  padding: 13px 20px 0px 0px;
}
.portfolio-section .client-description1 .client-text p {
  font-size: 14px;
  font-weight: 300;
  line-height: 22px;
}
.portfolio-section .client-description1 .client-text .purpple-text {
  font-size: 13px;
  font-weight: 300;
  line-height: 20px;
  margin-top: 40px;
  color: #474EFF;
}
.portfolio-section .client-description1 .client-text .client-web {
  color: #ffffff;
  font-size: 30px;
  font-weight: 300;
  line-height: 38px;
}
.portfolio-section .client-description1 .client-foto {
  display: flex;
  justify-content: flex-end;
}
.portfolio-section .client-description1 .client-foto img {
  max-height: 447px;
}
.portfolio-section .client-description2 {
  display: grid;
  grid-template-columns: 61% 31%;
  gap: 50px;
  margin-bottom: 250px;
}
.portfolio-section .client-description2 .client-text {
  grid-column: 2/3;
  float: right;
  padding: 13px 20px 0px 0px;
}
.portfolio-section .client-description2 .client-text p {
  font-size: 14px;
  font-weight: 300;
  line-height: 22px;
}
.portfolio-section .client-description2 .client-text .purpple-text {
  font-size: 13px;
  font-weight: 300;
  line-height: 20px;
  margin-top: 40px;
  color: #474EFF;
}
.portfolio-section .client-description2 .client-text .client-web {
  color: #ffffff;
  font-size: 30px;
  font-weight: 300;
  line-height: 38px;
}
.portfolio-section .client-description2 .client-foto {
  grid-column: 1/2;
}
.portfolio-section .client-description2 .client-foto img {
  max-height: 410px;
}
.portfolio-section .link-with-arrow {
  grid-column: 2/3;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-top: 40px;
}/*# sourceMappingURL=PortfolioSection.css.map */