.footer-main-container {
  margin-top: 80px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.footer-main-container .col-one {
  grid-column: 1/2;
}
.footer-main-container .col-one p {
  margin-top: 30px;
  font-weight: 300;
  font-size: 14px;
  line-height: 159%;
}
.footer-main-container .col-two {
  grid-column: 2/3;
  display: grid;
  grid-template-columns: 30% 1fr;
  gap: 8%;
}
.footer-main-container .col-two .col-title {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  grid-column: 1/2;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 22px;
  line-height: 27px;
}
.footer-main-container .col-two .col-data {
  grid-column: 2/3;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
}
.footer-main-container .col-two .newsletter-input {
  width: 90%;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #FFFFFF;
  padding-bottom: 10px;
  padding-top: 10px;
  margin-top: 10px;
  margin-bottom: 33px;
}
.footer-main-container .col-two .newsletter-input::-moz-placeholder {
  color: #ffffff;
  font-size: 14px;
  font-family: "Unbounded";
}
.footer-main-container .col-two .newsletter-input::placeholder {
  color: #ffffff;
  font-size: 14px;
  font-family: "Unbounded";
}
.footer-main-container .col-three {
  grid-column: 3/4;
  display: grid;
  grid-template-columns: 30% 1fr;
  gap: 8%;
}
.footer-main-container .col-three .col-title {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  grid-column: 1/2;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 22px;
  line-height: 27px;
}
.footer-main-container .col-three .col-data {
  grid-column: 2/3;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
}
.footer-main-container .col-three .col-data .contact-link {
  margin-top: 20px;
  display: block;
  color: #ffffff;
}
.footer-main-container .deco-icons-all {
  grid-column: 1/3;
  display: inline-flex;
  margin-top: 89px;
  margin-bottom: 89px;
  gap: 16px;
}/*# sourceMappingURL=FooterMain.css.map */