.bottom-bar-container{
    display: grid;
    grid-template-columns: 25% 1fr 25%;
    margin-bottom: 67px;
    
    .col-one{
        grid-column: 1/2;
        padding-right: 5%;
        display: flex;
        justify-content: space-between;
    }

    .col-two{
        grid-column: 2/3;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        .gray-link{
            color: #777777;
            font-weight: 300;
            font-size: 16px;
            line-height: 20px;
        }
    }

    .col-three{
        grid-column: 3/4;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        p{
            font-size: 16px;
            font-weight: 300;

        }
    }
}